import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { createOrder } from '../actions/orderActions'
import { CREATE_ORDER_RESET } from '../constants/orderConstants'

const PlaceOrderScreen = (props) => {
    const orderCreate  = useSelector(state=>state.orderCreate)
    const {loading,success,error,order} = orderCreate
    const cart = useSelector(state => state.cart)
    const toPrice = (num) => Number(num.toFixed(2))
    cart.itemsPrice = toPrice(
        cart.cartItems.reduce((a, c) => a + c.qty * c.price, 0)
    );
    cart.shippingPrice = cart.itemsPrice > 100 ? toPrice(0) : toPrice(10);
    cart.taxPrice = toPrice(0.15 * cart.itemsPrice);
    cart.totalPrice = cart.itemsPrice + cart.shippingPrice + cart.taxPrice;
    const dispatch = useDispatch();
    const placeOrderHandler = () => {
        dispatch(createOrder({...cart,orderItems: cart.cartItems}))
    }
    useEffect(()=>{
        if(success){
            props.history.push(`/order/${order._id}`)
            dispatch({type: CREATE_ORDER_RESET})
        }
    },[dispatch,props.history,order,success])
    return (
        <div className="row top">
            <div className="col-2">
                <ul>
                    <li>
                        <div className="card card-body">
                            <h2>Shipping</h2>
                            <p>
                                <strong>Name: </strong>{cart.shippingDetails.fullName}<br />
                                <strong>Address: </strong>{cart.shippingDetails.address},{cart.shippingDetails.city},{cart.shippingDetails.postalCode},{cart.shippingDetails.country}
                            </p>
                        </div>
                    </li>
                    <li>
                        <div className="card card-body">
                            <h2>Payment Method</h2>
                            <p>
                                <strong>Method: </strong>PayPal
                            </p>
                        </div>
                    </li>
                    <li>
                        <div className="card card-body">
                            <h2>Order Items</h2>
                            {cart.cartItems.length === 0 ? <h2>Cart is Empty. <Link to="/">Go Shopping</Link></h2> : <ul>
                                {cart.cartItems.map(item => {
                                    return <li key={item.product}>
                                        <div className="row">
                                            <div>
                                                <img src={item.image} alt={item.name} className="small"></img>
                                            </div>
                                            <div className="marginright-2">
                                                <Link to={`/product/${item.product}`}>{item.name}</Link>
                                            </div>
                                            <div>
                                                {item.qty} x ${item.price} = {item.qty * item.price}
                                            </div>
                                        </div>
                                    </li>
                                })}
                            </ul>
                            }
                        </div>
                    </li>
                </ul>
            </div>
            <div className="col-1">
                <div className="card card-body">
                    <ul>
                        <li>
                            <h2>Order Summary</h2>
                        </li>
                        <li>
                            <div className="row">
                                <div>Items</div>
                                <div>${cart.itemsPrice.toFixed(2)}</div>
                            </div>
                        </li>
                        <li>
                            <div className="row">
                                <div>Shipping</div>
                                <div>${cart.shippingPrice.toFixed(2)}</div>
                            </div>
                        </li>
                        <li>
                            <div className="row">
                                <div>Tax</div>
                                <div>${cart.taxPrice.toFixed(2)}</div>
                            </div>
                        </li>
                        <li>
                            <div className="row">
                                <div>
                                    <strong> Order Total</strong>
                                </div>
                                <div>
                                    <strong>${cart.totalPrice.toFixed(2)}</strong>
                                </div>
                            </div>
                        </li>
                        <li>
                            <button
                                type="button"
                                onClick={placeOrderHandler}
                                className="primary block"
                                disabled={cart.cartItems.length === 0}
                            >
                                Place Order
                            </button>
                        </li>
                        {loading && <h1>{loading}</h1>}
                        {error && <h1>{error}</h1>}
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default PlaceOrderScreen
